import React, { Component } from "react";
import Header from "./Header";
import { HashRouter as Router, Route } from "react-router-dom";
import Login from "./Login";
import Home from "./Home";
import Shezhi from "./Shezhi";
import Helpcenter from "./Helpcenter";
import Callme from "./Callme";
import Customer from "./Customer";
import Add from "./Add";
import Bianji from "./Bianji";
import Huodongguanli from "./Huodongguanli";
import Addhuodong from "./Addhuodong";
import Bianjihuodong from "./Bianjihuodong";
import Huodong from "./Huodong";
import Chakanbaogao from "./Chakanbaogao";
class Mian extends Component {
  render() {
    return (
      <Router>
        <Header />
        <Route exact path="/" component={Login} />
        <Route path="/header" component={Home} />
        <Route path="/shezhi" component={Shezhi} />
        <Route path="/helpcenter" component={Helpcenter} />
        <Route path="/callme" component={Callme} />
        <Route path="/customer" component={Customer} />
        <Route path="/add" component={Add} />
        <Route path="/bianji" component={Bianji} />
        <Route path="/huodongguanli/:id" component={Huodongguanli} />
        <Route path="/addhuodong/:id" component={Addhuodong} />
        <Route path="/bianjihuodong/:id" component={Bianjihuodong} />
        <Route path="/huodong/:id" component={Huodong} />
        <Route path="/chakanbaogao" component={Chakanbaogao} />
      </Router>
    );
  }
}
export default Mian;
